<template>
	<div id="root" :class="{shiftRight:parentIsNotRoot}">
		<div 
		id="topLine"
		ref="topLine"
		:class="{flex:true, border:true, selected:isSelected, focused:isFocused, hidden:isRoot}" 
		@click="focus"
		@contextmenu.prevent="onRightClick"
	
		>
			<button
			@click="toggleButton"
			:class="{boldText:hasChildren}"
			> 
				{{button_text}} 
			</button>
			
			<textarea 
			ref="textarea"
			type="text" 
			v-show="isEditable"
			class="contentBox"
			v-model:value="markup"
			>	
			</textarea>

			<span 
			id="contentSpan"
			ref="contentSpan"
			v-show="!isEditable"
			v-html="markup"
			class="contentBox">
			</span>
		</div>
		
		
			<keep-alive>
				<div v-if="isExpanded" id="childrenDiv" ref="childrenDiv">
					<NoteInstance  v-for="child in tempChildren" :noteid="child" :key="child"/>
					<NoteInstance  v-for="child in children" :noteid="child" :key="child"/>
				</div>
			</keep-alive>
		

		<vue-context ref="contextmenu">
		    <li>
		        <a href="#" @click.prevent="noteContainer.DeepCopy">Deep Copy</a>
		    </li>
		    <li>
		        <a href="#" @click.prevent="noteContainer.hardDeleteAllSelectedInstances">Hard Delete</a>
		    </li>
		<!--     <li>
		        <a href="#" @click.prevent="ctxmenuOptionOnClick($event.target.innerText)">Paste (Ctrl+v)</a>
		    </li> -->
		</vue-context>

	</div>
</template>


<script>
import {mapState, mapGetters} from 'vuex';
import { VueContext } from 'vue-context';

export default {

	name: 'NoteInstance',
	
	components: {
		VueContext
	},

	props:{
		noteid: Number
	},

	data(){
		
		return {
			
			noteContainer: this.$parent.noteContainer,
			downArrow: 'Ë…',
			rightArrow: '>',
			button_text: '>',
			isEditable: false,
			tempChildren: [],
			tempLinkCount: 0
		}
	},

	methods: {
		//Test method to see behavior of ctxMenu properties
		ctxmenuOptionOnClick(innerText){
			console.log(`Innertext is ${innerText}`);
		},

		onRightClick(event,data){
			this.$refs.contextmenu.open(event,data);
			this.focus(event);
		},

		resize(){
			var tls = this.$refs.topLine.style;
			var contentBox = this.isEditable ? this.$refs.textarea : this.$refs.contentSpan
			
			tls.height = 0;
			tls.height = contentBox.scrollHeight + "px";
		},

		focus(event){
			var appendMode = event ? event.ctrlKey : false;
			this.noteContainer.focus(this, appendMode);
		},
		
		addNote(note){
			this.$store.commit("addChildToParent",{child: note, parent: this.note});
		},

		tryAddChild(childInstance){
			if (this.children.includes(childInstance.noteid)){return;}
			this.addChild(childInstance);
		},

		addChild(childInstance){
			this.$store.commit("addChildToParent",{child: childInstance.note, parent: this.note});
		},

		tryAddTempChild(id){
			if (this.children.includes(id) || this.tempChildren.includes(id)){return }
			this.tempChildren = [...this.tempChildren, id];
			this.expand();
		},

		addInlineTempChildLink(childInstance){
			debugger;
			if (childInstance == this){return; }

			if (this.isFocused && this.isEditable){
				window.tempLinkFunctions={};
				this.tempLinkCount++;
				var uid = this._uid + "d" + this.tempLinkCount;
				window.tempLinkFunctions[uid]= function(){this.tryAddTempChild(childInstance.noteid)}.bind(this);
				var leftText = `<a onclick="window.noteContainer.addTempChild(${childInstance.noteid})" style="text-decoration: underline; color: forestgreen">`;
				//var leftText = `<a onclick="debugger;console.log(this.parentNode.id)" style="text-decoration: underline; color: forestgreen">`;
				var rightText = "</a>";
				this.addTextWRTCursor(leftText,rightText);		
			}
		},

		addTextWRTCursor(textLeftOfCursor,textRightOfCursor){
			var text = this.markup;
			var start = this.textarea.selectionStart;
			var end = this.textarea.selectionEnd;
			this.markup = text.slice(0,start) + textLeftOfCursor + text.slice(start,end) + textRightOfCursor +  text.slice(end,text.length);
			setTimeout(function(){
			this.textarea.selectionStart = start + textLeftOfCursor.length;
			this.textarea.selectionEnd = end + textLeftOfCursor.length;	
			}.bind(this), 10)
			
			
		},

		//getPreviousVisibleInstance, getNextVisibleInstance, and getLastChild are getters because if they
		//are properties the property doesn't update on some shift+up/down-s 
		getPreviousVisibleInstance(){
			var idx = this.getIndexAsSibling();
			if (this.$parent == this.noteContainer && idx == 0){return this}
				

			var lastvi = this.getPreviousSibling();
			if (lastvi) { 
				while(lastvi.isExpanded && lastvi.hasChildren){
					lastvi = lastvi.getLastChild();
				}
				return lastvi;
			}
			else{
				return this.$parent
			}
		},

		//Null if this is the last visible instance
		getNextVisibleInstance(){
			//If this is expanded and has children, return the first one
			if(this.isExpanded && this.hasChildren){
				var firstChildNoteID = this.tempChildren.length>0 ? this.tempChildren[0] : this.note.children[0];
				var firstChildInstance; 
				for (var i=0; i<this.$children.length; i++){
					if (firstChildInstance){break;}
					var c = this.$children[i];
					if (c.noteid == firstChildNoteID){firstChildInstance=c;}
				}
				return firstChildInstance;
			}

			var recFn = function(instance){
				if (instance.isRoot){return instance}
				var nextSib = instance.getNextSibling(); 
				if (nextSib != null){return nextSib}
				return recFn(instance.$parent);
				
			}

			var ret=recFn(this);
			return ret.isRoot ? null : ret;
		},

		//Return previous instance, where order is defined by the note. If there is none, return null.
		getPreviousSibling(){
			//todo redo this. something like 
			var prevSib,noteId; 
			var union = this.$parent.tempChildren.concat(this.$parent.children);
			for (var i=0; i<union.length; i++){
				noteId = union[i];
				if (noteId==this.noteid){break;}
				prevSib = noteId;
			}

			return this.$parent.getChildInstanceById(prevSib);


			// if (this.$parent.tempChildren.includes(this.noteid)){

			// }

			// else{
			// 	var siblings = this.$parent.note.children; 
			// 	var index = siblings.indexOf(this.note.id);
			// 	if (index<1){return null}
			// 	var prevSibNoteIndx = siblings[index-1];
			// 	var prevSibNote = this.$store.state.notes[prevSibNoteIndx];
			// 	var prevSibInstance = this.$parent.getChildInstances().filter(inst => inst.note==prevSibNote)[0];
			// 	return prevSibInstance;
			// }
			
		},

		//Symmetric to above
		getNextSibling(){
			
			var siblings = this.$parent.tempChildren.concat(this.$parent.note.children); 
			var index = siblings.indexOf(this.note.id);
			if (index == siblings.length-1){return null} 
			var nextSibNoteIndx=siblings[index+1];
			var nextSibNote = this.$store.state.notes[nextSibNoteIndx];
			var nextSibInstance = this.$parent.getChildInstances().filter(inst => inst.note==nextSibNote)[0];
			return nextSibInstance;
		},
			

		getLastChild(){return this.childInstances[this.childInstances.length-1]},

		toggleButton(){this.button_text = this.button_text == this.rightArrow ? this.downArrow : this.rightArrow},
		
		expand(){this.button_text = this.downArrow},

		expand_N_levels(N){
			if (N >= 1){this.expand()}
			if (N > 1) {
				this.$nextTick(function(){
					this.getChildInstances().forEach(function(childInst){
						childInst.expand_N_levels(N-1);
					})
				}.bind(this))	
			}
		},

		collapse_N_levels(N){
			if (N == 0){this.collapse(); }
			else{
				this.getChildInstances().forEach(function(child){
					child.collapse_N_levels(N-1);
				})
			}
		},
		
		collapse(){this.button_text = this.rightArrow},
		
		removeFromParent(){
			if (this.$parent.note.children.includes(this.noteid))
			this.$store.commit("removeChildFromParent",{child: this.note, parent: this.$parent.note});
			if (this.$parent.tempChildren.includes(this.noteid))
			this.$parent.tempChildren = this.$parent.tempChildren.filter((x)=>{x!=this.noteid})

		},
		
		getChildInstances(){
			var children = this.$children
			var ret=children.filter(comp => comp.note!=undefined);
			return ret;
		},
		
		getChildInstance(note){
			return this.getChildInstanceById(note.id);
		},

		getChildInstanceById(noteId){
			if (!noteId){return null}
			return this.$children.filter(inst=>inst.noteid==noteId)[0];
		},
		
		getIndexAsSibling(){
			if(this.isRoot){return 0}
			var tmpArrIdx = this.$parent.tempChildren.indexOf(this.noteid); 
			return tmpArrIdx!=-1 ? tmpArrIdx : this.$parent.tempChildren.length + this.$parent.childInstances.indexOf(this)
		},
		
		//tag=b|i|u . Looks at previous text and adds an opening or closing tag as appropriate.
		addTag(tag){
				//Add a closing tag iff there is a last tag that was the opening tag
				var addClosingTag = function(tag){
					var regex;
					switch(tag){
						case 'b':
							regex = /<\/?b>/g;
							break;
						case 'u':
							regex = /<\/?u>/g;
							break;
						case 'i':
							regex = /<\/?i>/g;
							break;
					}
					var matches = this.markup.match(regex);

					if (matches == undefined){return false}

					var last_match = matches[matches.length-1];
					if (last_match == "</"+tag+">"){return false}
					
					return true;

				}.bind(this);
				
				var strToAdd = "<" + (addClosingTag(tag) ? "/" : "") +  tag + ">";
				this.markup += strToAdd;
		},

		hardDelete(){
			this.note.parents.forEach(function(parent_ID){
				this.$store.commit("removeChildFromParent",{child:this.note, parent:this.allNotes[parent_ID]});
			}.bind(this));		
		},

		isTempInstance(){ 
			return this.$parent.tempChildren.includes(this.noteid);
		}
	},
 
	computed: {
		...mapState({
			children(state){
				return state.notes[this.noteid].children;
			},

			note(state){
				return state.notes[this.noteid];
			},

			allNotes(state){
				return state.notes;
			},

			parentNote(state){
				if (this.isRoot){ return undefined}
				return state.notes[this.$parent.note.id];
			},

		
		}),

		markup: {
			set(markup){
				this.$store.commit('setMarkup',{note:this.note, markup});
				this.resize();

			},
			get(){return this.note.markup}
		},

		//Array of instances from (noteContainer,this]
		path(){
			if (this.$parent == this.noteContainer){return [this]}
			return [...this.$parent.path,this]
		},

		isVisible(){
			if (this.$parent == this.noteContainer){return true}
			if (this.$parent.isVisible && this.$parent.isExpanded){return true}
			return false
		},

		//Sometimes buggy. Not sure why. Prefer calling the function directly.
		//May be able to find more info here:
		//https://vuejs.org/v2/guide/reactivity.html#Change-Detection-Caveats
		childInstances(){return this.getChildInstances()},

		isSelected(){ return this.noteContainer.selectedInstances.includes(this)},
		isFocused(){ return this.noteContainer.focusedInstance == this},
		isExpanded(){ return this.button_text == this.downArrow},
		indexAsSibling(){return this.getIndexAsSibling()},
		textarea(){return this.$refs.textarea},
		hasChildren(){
			return this.note.children.length>0 || this.tempChildren.length>0;
		},
		isRoot(){return this.noteid==0},
		parentIsNotRoot(){return !this.$parent.isRoot},
		level(){return this.isRoot ? 0 : this.$parent.level+1}
	},

	watch: {
		isEditable : function(newValue, oldValue){
			this.$nextTick(function(){
				this.resize();
				if(this.textarea){this.textarea.focus()}
			});

			//markup is bound to the textarea, so as we change the editability of the textarea,
			//we change the markup to be what the user expects/wants 
			if (newValue == true){
				this.markup = this.markup.replace("&lt","/<")
										.replace("&gt","/>");

			}

			if (newValue == false){
				this.markup = this.markup.replace("/<","&lt")
										.replace("/>","&gt");

				
			}
		},	
		

		markup : function(){
			if (!document.title.endsWith('*')){document.title += '*'}
			window.tnk=this.textarea;
		},

		children : function(){
			if (!document.title.endsWith('*')){document.title += '*'}
		}
	},

	mounted(){
		this.resize();
		
		//Autoexpand root and autofocus it's first child 
		if (this.isRoot){
			this.focus();
			this.button_text = this.downArrow;
		}
	}

};

</script>

<style lang="scss">
	@import '~vue-context/src/sass/vue-context';
</style>

<style scoped>

	button {
			padding: 0;
			border: 0;
			padding: 3px;
			border-right: 1px solid black;
			outline: none;
		}

	textarea {
		width: 100%;
		overflow-y: hidden;
		outline: none;
		border-left: none;
	}

	span {
		white-space: pre-wrap;
	}

	a {
		text-decoration: underline;
		text-decoration-color: blue;
	}

	#root {
		width: 100%;
		overflow: auto;
	}

	#contentSpan {
		padding-left: 2px;
		min-height: 17px;
	}

	#childrenDiv{
		display: flow-root;
		width: calc(100% - 100px);
		float: right;
		
	}

	#NoteContainer > #root > #childrenDiv{
		width: 100%;
		
	}


	.shiftRight{
		margin-top: 1px;
		float: right;
		width: 97%;

	}

	#topLine{
		height: auto;
		overflow-y: hidden;
	}

	.flex {
		display: flex;
		flex-basis: min-content;
	}

	.contentBox {
		background: white;
		width: 100%;
	}


	.border {
		border: 1px solid black;
	}


	.selected { 
	    border-color: #4d94ff;
	    border-width: 2px;
	}

	.focused { 
	    border-color: red;
	    border-width: 2px;
	}

	.boldText{
		font-weight: bold
	}

	.hidden {
		display: none;
	}

</style>